import { doRegister } from '@/network/register.js'
import { isPasswordAvailable } from '@/common/index.js'
import { mapMutations, mapActions } from 'vuex'



export default {
	data() {
		return {
			userPhone: '',
			password1: '',
			password2: '',
			showPassword1Input: false,
			showPassword2Input: false
		}
	},
	methods:{
		...mapMutations(['setLogin']),
		...mapActions(['doGetMyUserInfo']),
		//注册按钮
		registerClick(e) {
			const val = e.detail.value;
			if(val.password1.length> 0 && val.password2.length> 0) {
				if(val.password1 === val.password2) {
					if(isPasswordAvailable(val.password1)) {
						const obj = {
							userPhone: this.userPhone,
							userPassword: val.password2
						}
						doRegister(obj).then(res => {
							console.log(res)
							if(res.data.code === 4003) {
								uni.showToast({
									title: res.data.msg,
									icon: 'none'
								})
							}
							if(res.data.code === 2000) {
								const userInfo = res.data.data
								
								//将用户微信息保存（全局userInfo、hasLogin和本地仓储）
								this.setLogin(userInfo)
								
								//获取用户详细信息
								this.doGetMyUserInfo()
								
								uni.showToast({
									title: '注册成功',
									icon: 'none'
								})
								//关闭所有页面，跳转到index页面
								uni.reLaunch({
									url: '/pages/index/index'
								})
							}
						}).catch(err => {
							uni.showToast({
								title: '服务器发生错误，请稍后重试',
								icon: 'none'
							})
							console.log(err)
						})
					}else {
						uni.showToast({
							title: '密码为8-16位任意字符',
							icon: 'none'
						})
					}
					
				}else {
					uni.showToast({
						title: '两次密码不一致',
						icon: 'none'
					})
				}
			}else {
				uni.showToast({
					title: '密码不能为空',
					icon: 'none'
				})
			}
			
			
		},
		//监听pwd1输入
		handlerPassword1(e) {
			if(e.detail.value.length > 0) {
				this.showPassword1Input = true
			}else {
				this.showPassword1Input = false
			}
		},
		//监听pwd2输入
		handlerPassword2(e) {
			if(e.detail.value.length > 0) {
				this.showPassword2Input = true
			}else {
				this.showPassword2Input = false
			}
		},
		
		//清除pwd1输入
		delPwd1Input() {
			this.showPassword1Input = false;
			this.password1 = '';
		},
		
		//清除pwd2输入
		delPwd2Input() {
			this.showPassword2Input = false;
			this.password2 = '';
		}
	},
	onLoad(option) {
		console.log(option.userPhone)
		this.userPhone = option.userPhone
	}
	
}
